import React from 'react';
import { useParams } from 'react-router-dom';
import {
  Box,
  Container,
  Typography,
  Card,
  CardMedia,
  CardContent,
  Breadcrumbs,
  Link as MuiLink,
  CircularProgress,
  Divider,
} from '@mui/material';
import { Link as RouterLink } from 'react-router-dom';
import { useNewsById } from '../hooks/useNews';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';

const NewsDetailPage = () => {
  const { id } = useParams<{ id: string }>();
  const { data: news, isLoading } = useNewsById(id || '');

  if (isLoading) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minHeight="400px">
        <CircularProgress />
      </Box>
    );
  }

  if (!news) {
    return (
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom>
          Новость не найдена
        </Typography>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Breadcrumbs
        separator={<NavigateNextIcon fontSize="small" />}
        aria-label="breadcrumb"
        sx={{ mb: 4 }}
      >
        <MuiLink component={RouterLink} to="/" color="inherit">
          Главная
        </MuiLink>
        <MuiLink component={RouterLink} to="/news" color="inherit">
          Новости
        </MuiLink>
        <Typography color="text.primary">{news.title}</Typography>
      </Breadcrumbs>

      <Card sx={{ mb: 4 }}>
        <CardMedia
          component="img"
          height="400"
          image={news.image}
          alt={news.title}
          sx={{
            objectFit: 'cover',
            backgroundColor: 'background.paper',
          }}
        />
        <CardContent>
          <Typography variant="h4" component="h1" gutterBottom>
            {news.title}
          </Typography>
          <Typography variant="subtitle1" color="text.secondary" gutterBottom>
            {news.date}
          </Typography>
          <Divider sx={{ my: 2 }} />
          <Typography variant="body1" paragraph>
            {news.description}
          </Typography>
          <Box sx={{ whiteSpace: 'pre-line' }}>
            <Typography variant="body1">
              {news.content}
            </Typography>
          </Box>
        </CardContent>
      </Card>
    </Container>
  );
};

export default NewsDetailPage; 