import { Car, CarFilters } from '../types/car';
import carsData from '../../public/data/cars.json';

const API_BASE_URL = '/data';

export const getCars = async (filters?: CarFilters): Promise<Car[]> => {
  // Simulate API delay
  await new Promise(resolve => setTimeout(resolve, 500));

  let filteredCars = [...carsData.cars];

  if (filters?.searchQuery) {
    const query = filters.searchQuery.toLowerCase();
    filteredCars = filteredCars.filter(
      car => car.brand.toLowerCase().includes(query) ||
              car.model.toLowerCase().includes(query)
    );
  }

  if (filters?.brand) {
    filteredCars = filteredCars.filter(car => car.brand === filters.brand);
  }

  if (filters?.yearRange) {
    const [start, end] = filters.yearRange;
    filteredCars = filteredCars.filter(car => 
      car.year >= start && 
      car.year <= end
    );
  }

  if (filters?.priceRange) {
    const [start, end] = filters.priceRange;
    filteredCars = filteredCars.filter(car => 
      car.priceRUB >= start && 
      car.priceRUB <= end
    );
  }

  if (filters?.transmission) {
    filteredCars = filteredCars.filter(car => car.transmission === filters.transmission);
  }

  if (filters?.fuelType) {
    filteredCars = filteredCars.filter(car => car.fuelType === filters.fuelType);
  }

  if (filters?.auctionGrade) {
    filteredCars = filteredCars.filter(car => car.auctionGrade === filters.auctionGrade);
  }

  return filteredCars;
};

export const getLatestCars = async (limit: number = 5): Promise<Car[]> => {
  // Simulate API delay
  await new Promise(resolve => setTimeout(resolve, 500));
  
  // Sort cars by auction date in descending order and take the latest ones
  return [...carsData.cars]
    .sort((a, b) => new Date(b.auctionDate!).getTime() - new Date(a.auctionDate!).getTime())
    .slice(0, limit);
};

export const getCarById = async (id: string): Promise<Car | undefined> => {
  // Simulate API delay
  await new Promise(resolve => setTimeout(resolve, 500));
  return carsData.cars.find(car => car.id === id);
};

export const calculatePrice = async (id: string): Promise<{
  carPrice: number;
  shipping: number;
  customs: number;
  total: number;
}> => {
  // Simulate API delay
  await new Promise(resolve => setTimeout(resolve, 500));
  
  const car = carsData.cars.find(car => car.id === id);
  if (!car) {
    throw new Error('Car not found');
  }

  // Simplified price calculation
  const shipping = 150000; // Fixed shipping cost
  const customs = car.priceRUB * 0.3; // 30% customs duty

  return {
    carPrice: car.priceRUB,
    shipping,
    customs,
    total: car.priceRUB + shipping + customs
  };
}; 