import React, { useState } from 'react';
import {
  Box,
  Container,
  Grid,
  Card,
  CardMedia,
  CardContent,
  Typography,
  TextField,
  InputAdornment,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Button,
  Slider,
  Chip,
  CircularProgress,
  SelectChangeEvent,
} from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import { Link as RouterLink } from 'react-router-dom';
import { useCars } from '../hooks/useCars';
import { CarFilters } from '../types/car';

const marks = [
  { value: 2010, label: '2010' },
  { value: 2015, label: '2015' },
  { value: 2020, label: '2020' },
  { value: 2025, label: '2025' },
];

const CarListPage = () => {
  const [filters, setFilters] = useState<CarFilters>({
    searchQuery: '',
    brand: '',
    yearRange: [2010, 2025] as [number, number],
    priceRange: [0, 2000000] as [number, number],
  });

  const { data: cars, isLoading } = useCars(filters);

  const handleYearChange = (event: Event, newValue: number | number[]) => {
    setFilters(prev => ({ ...prev, yearRange: newValue as [number, number] }));
  };

  const handlePriceChange = (event: Event, newValue: number | number[]) => {
    setFilters(prev => ({ ...prev, priceRange: newValue as [number, number] }));
  };

  const handleSearchChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setFilters(prev => ({ ...prev, searchQuery: event.target.value }));
  };

  const handleBrandChange = (event: SelectChangeEvent) => {
    setFilters(prev => ({ ...prev, brand: event.target.value }));
  };

  const resetFilters = () => {
    setFilters({
      searchQuery: '',
      brand: '',
      yearRange: [2010, 2025] as [number, number],
      priceRange: [0, 2000000] as [number, number],
    });
  };

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Grid container spacing={3}>
        {/* Filters Sidebar */}
        <Grid item xs={12} md={3}>
          <Box sx={{ p: 2, bgcolor: 'background.paper', borderRadius: 2 }}>
            <Typography variant="h6" gutterBottom>
              Фильтры
            </Typography>

            <TextField
              fullWidth
              label="Поиск"
              variant="outlined"
              value={filters.searchQuery}
              onChange={handleSearchChange}
              sx={{ mb: 2 }}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <SearchIcon />
                  </InputAdornment>
                ),
              }}
            />

            <FormControl fullWidth sx={{ mb: 2 }}>
              <InputLabel>Марка</InputLabel>
              <Select
                value={filters.brand}
                label="Марка"
                onChange={handleBrandChange}
              >
                <MenuItem value="toyota">Toyota</MenuItem>
                <MenuItem value="honda">Honda</MenuItem>
                <MenuItem value="nissan">Nissan</MenuItem>
                <MenuItem value="mazda">Mazda</MenuItem>
                <MenuItem value="subaru">Subaru</MenuItem>
              </Select>
            </FormControl>

            <Typography gutterBottom>Год выпуска</Typography>
            <Slider
              value={filters.yearRange}
              onChange={handleYearChange}
              valueLabelDisplay="auto"
              min={2010}
              max={2025}
              marks={marks}
              sx={{ mb: 4 }}
            />

            <Typography gutterBottom>Цена (₽)</Typography>
            <Slider
              value={filters.priceRange}
              onChange={handlePriceChange}
              valueLabelDisplay="auto"
              min={0}
              max={2000000}
              step={100000}
              sx={{ mb: 2 }}
            />

            <Button
              variant="contained"
              fullWidth
              onClick={resetFilters}
            >
              Сбросить фильтры
            </Button>
          </Box>
        </Grid>

        {/* Car List */}
        <Grid item xs={12} md={9}>
          {isLoading ? (
            <Box display="flex" justifyContent="center" alignItems="center" minHeight="400px">
              <CircularProgress />
            </Box>
          ) : (
            <Grid container spacing={3}>
              {cars?.map((car) => (
                <Grid item key={car.id} xs={12} sm={6} md={4}>
                  <Card
                    component={RouterLink}
                    to={`/cars/${car.id}`}
                    sx={{
                      height: '100%',
                      display: 'flex',
                      flexDirection: 'column',
                      textDecoration: 'none',
                      transition: 'transform 0.2s',
                      borderRadius: 4,
                      '&:hover': {
                        transform: 'scale(1.02)',
                      },
                    }}
                  >
                    <CardMedia
                      component="img"
                      sx={{
                        height: 200,
                        objectFit: 'cover',
                        backgroundColor: 'background.paper',
                        borderTopLeftRadius: 16,
                        borderTopRightRadius: 16,
                      }}
                      image={car.images[0] || "/images/cars/placeholder.svg"}
                      alt={`${car.brand} ${car.model}`}
                    />
                    <CardContent sx={{ flexGrow: 1 }}>
                      <Typography gutterBottom variant="h5" component="h2">
                        {car.brand} {car.model}
                      </Typography>
                      <Typography variant="body2" color="text.secondary" gutterBottom>
                        {car.year} • {car.complectation} • {car.transmission} • {car.color}
                      </Typography>
                      <Typography variant="h6" color="primary">
                        {car.priceRUB.toLocaleString()} ₽
                      </Typography>
                      <Box sx={{ mt: 1 }}>
                        <Chip 
                          label={`${car.condition} аукцион`} 
                          color="success" 
                          size="small" 
                          sx={{ mr: 1 }} 
                        />
                      </Box>
                    </CardContent>
                  </Card>
                </Grid>
              ))}
            </Grid>
          )}
        </Grid>
      </Grid>
    </Container>
  );
};

export default CarListPage; 