import { useState } from 'react';
import {
  Box,
  Container,
  Grid,
  Typography,
  Card,
  Button,
  List,
  ListItem,
  ListItemText,
  Divider,
  Chip,
  TextField,
  Alert,
  Dialog,
  IconButton,
  useTheme,
  useMediaQuery,
} from '@mui/material';
import { useParams } from 'react-router-dom';
import LocalShippingIcon from '@mui/icons-material/LocalShipping';
import AttachMoneyIcon from '@mui/icons-material/AttachMoney';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import React from 'react';
import { useCar, useCarPrice } from '../hooks/useCars';
import { formatPrice } from '../utils/format';
import CloseIcon from '@mui/icons-material/Close';
import NavigateBeforeIcon from '@mui/icons-material/NavigateBefore';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';

const CarDetailsPage = () => {
  const { id } = useParams();
  const [name, setName] = useState('');
  const [phone, setPhone] = useState('');
  const [email, setEmail] = useState('');
  const [selectedImage, setSelectedImage] = useState<string | null>(null);
  const [currentImageIndex, setCurrentImageIndex] = useState(0);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('md'));
  const { data: car, isLoading: isCarLoading } = useCar(id || '');
  const { data: price, isLoading: isPriceLoading } = useCarPrice(id || '');

  const handleImageClick = (image: string, index: number) => {
    setSelectedImage(image);
    setCurrentImageIndex(index);
  };

  const handleClose = () => {
    setSelectedImage(null);
  };

  const handlePrevious = () => {
    if (car?.images && currentImageIndex > 0) {
      setCurrentImageIndex(currentImageIndex - 1);
      setSelectedImage(car.images[currentImageIndex - 1]);
    }
  };

  const handleNext = () => {
    if (car?.images && currentImageIndex < car.images.length - 1) {
      setCurrentImageIndex(currentImageIndex + 1);
      setSelectedImage(car.images[currentImageIndex + 1]);
    }
  };

  if (isCarLoading || isPriceLoading) {
    return (
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Typography>Загрузка...</Typography>
      </Container>
    );
  }

  if (!car) {
    return (
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Alert severity="error">Автомобиль не найден</Alert>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Grid container spacing={4}>
        {/* Car Images */}
        <Grid item xs={12} md={8}>
          <Card sx={{ p: 2, mb: 2, borderRadius: 4 }}>
            <Box
              component="img"
              src={car.images[0] || "/images/cars/placeholder.svg"}
              alt={`${car.brand} ${car.model}`}
              sx={{ 
                width: '100%', 
                height: 'auto', 
                borderRadius: 4,
                cursor: 'pointer',
                transition: 'transform 0.2s',
                '&:hover': {
                  transform: 'scale(1.02)',
                },
              }}
              onClick={() => handleImageClick(car.images[0], 0)}
            />
          </Card>
          <Grid container spacing={2}>
            {car.images.slice(1).map((image, index) => (
              <Grid item xs={4} key={index}>
                <Card sx={{ p: 1, borderRadius: 4 }}>
                  <Box
                    component="img"
                    src={image}
                    alt={`${car.brand} ${car.model} ${index + 2}`}
                    sx={{ 
                      width: '100%', 
                      height: 'auto', 
                      borderRadius: 4,
                      cursor: 'pointer',
                      transition: 'transform 0.2s',
                      '&:hover': {
                        transform: 'scale(1.02)',
                      },
                    }}
                    onClick={() => handleImageClick(image, index + 1)}
                  />
                </Card>
              </Grid>
            ))}
          </Grid>
        </Grid>

        {/* Car Details */}
        <Grid item xs={12} md={4}>
          <Card sx={{ p: 3, mb: 3, borderRadius: 4 }}>
            <Typography variant="h4" gutterBottom>
              {car.brand} {car.model}
            </Typography>
            <Typography variant="h5" color="primary" gutterBottom>
              {formatPrice(car.priceRUB)}
            </Typography>
            <Typography variant="body2" color="text.secondary" gutterBottom>
              {formatPrice(car.priceJPY)} ¥
            </Typography>

            <Box sx={{ my: 2 }}>
              <Chip
                icon={<CheckCircleIcon />}
                label={`Аукцион ${car.condition}`}
                color="success"
                sx={{ mr: 1 }}
              />
              <Chip label={`${car.year} год`} sx={{ mr: 1 }} />
            </Box>

            <List>
              {car.features.map((feature, index) => (
                <ListItem key={index}>
                  <ListItemText primary={feature} />
                </ListItem>
              ))}
            </List>
          </Card>

          {/* Price Calculation */}
          <Card sx={{ p: 3, mb: 3, borderRadius: 4 }}>
            <Typography variant="h6" gutterBottom>
              Расчет стоимости
            </Typography>
            <List>
              <ListItem>
                <ListItemText primary="Стоимость автомобиля" />
                <Typography>{formatPrice(price?.carPrice || 0)}</Typography>
              </ListItem>
              <ListItem>
                <ListItemText primary="Доставка" />
                <Typography>{formatPrice(price?.shipping || 0)}</Typography>
              </ListItem>
              <ListItem>
                <ListItemText primary="Таможенные платежи" />
                <Typography>{formatPrice(price?.customs || 0)}</Typography>
              </ListItem>
              <Divider />
              <ListItem>
                <ListItemText primary="Итого" />
                <Typography variant="h6" color="primary">
                  {formatPrice(price?.total || 0)}
                </Typography>
              </ListItem>
            </List>
          </Card>

          {/* Contact Form */}
          <Card sx={{ p: 3, borderRadius: 4 }}>
            <Typography variant="h6" gutterBottom>
              Заказать консультацию
            </Typography>
            <Box component="form" sx={{ mt: 2 }}>
              <TextField
                fullWidth
                label="Ваше имя"
                value={name}
                onChange={(e) => setName(e.target.value)}
                sx={{ mb: 2 }}
              />
              <TextField
                fullWidth
                label="Телефон"
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
                sx={{ mb: 2 }}
              />
              <TextField
                fullWidth
                label="Email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                sx={{ mb: 2 }}
              />
              <Button
                variant="contained"
                fullWidth
                size="large"
                startIcon={<LocalShippingIcon />}
              >
                Заказать автомобиль
              </Button>
            </Box>
          </Card>
        </Grid>

        {/* Image Preview Dialog */}
        <Dialog
          fullScreen={fullScreen}
          open={!!selectedImage}
          onClose={handleClose}
          maxWidth="lg"
          sx={{
            '& .MuiDialog-paper': {
              backgroundColor: 'rgba(0, 0, 0, 0.9)',
            },
          }}
        >
          <Box sx={{ position: 'relative' }}>
            <IconButton
              edge="start"
              color="inherit"
              onClick={handleClose}
              aria-label="close"
              sx={{
                position: 'absolute',
                right: 8,
                top: 8,
                color: 'white',
              }}
            >
              <CloseIcon />
            </IconButton>
            <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', minHeight: '100vh' }}>
              <IconButton
                onClick={handlePrevious}
                disabled={currentImageIndex === 0}
                sx={{
                  position: 'absolute',
                  left: 16,
                  color: 'white',
                }}
              >
                <NavigateBeforeIcon />
              </IconButton>
              <Box
                component="img"
                src={selectedImage || ''}
                alt={`${car.brand} ${car.model} ${currentImageIndex + 1}`}
                sx={{
                  maxWidth: '100%',
                  maxHeight: '100vh',
                  objectFit: 'contain',
                }}
              />
              <IconButton
                onClick={handleNext}
                disabled={currentImageIndex === (car?.images.length || 0) - 1}
                sx={{
                  position: 'absolute',
                  right: 16,
                  color: 'white',
                }}
              >
                <NavigateNextIcon />
              </IconButton>
            </Box>
          </Box>
        </Dialog>
      </Grid>
    </Container>
  );
};

export default CarDetailsPage; 