import { useQuery } from '@tanstack/react-query';
import * as carService from '../services/carService';
import { Car, CarFilters } from '../types/car';

export const useCars = (filters?: CarFilters) => {
  return useQuery({
    queryKey: ['cars', filters],
    queryFn: () => carService.getCars(filters),
    gcTime: 30 * 60 * 1000, // 30 minutes
    staleTime: 5 * 60 * 1000, // 5 minutes
  });
};

export const useLatestCars = (limit: number = 6) => {
  return useQuery({
    queryKey: ['latestCars', limit],
    queryFn: () => carService.getLatestCars(limit),
    gcTime: 30 * 60 * 1000, // 30 minutes
    staleTime: 5 * 60 * 1000, // 5 minutes
  });
};

export const useCar = (id: string) => {
  return useQuery({
    queryKey: ['car', id],
    queryFn: () => carService.getCarById(id),
    enabled: !!id,
    gcTime: 30 * 60 * 1000, // 30 minutes
    staleTime: 5 * 60 * 1000, // 5 minutes
  });
};

export const useCarPrice = (id: string) => {
  return useQuery({
    queryKey: ['carPrice', id],
    queryFn: () => carService.calculatePrice(id),
    enabled: !!id,
    gcTime: 30 * 60 * 1000, // 30 minutes
    staleTime: 5 * 60 * 1000, // 5 minutes
  });
}; 