import { News, NewsFilters } from '../types/news';
import newsData from '../data/news.json';

export const getNews = async (filters?: NewsFilters): Promise<News[]> => {
  // Simulate API delay
  await new Promise(resolve => setTimeout(resolve, 500));

  let filteredNews = [...newsData.news];

  if (filters?.searchQuery) {
    const query = filters.searchQuery.toLowerCase();
    filteredNews = filteredNews.filter(
      news => news.title.toLowerCase().includes(query) ||
              news.description.toLowerCase().includes(query)
    );
  }

  const startDate = filters?.dateRange?.start;
  const endDate = filters?.dateRange?.end;
  
  if (startDate && endDate) {
    filteredNews = filteredNews.filter(news => {
      const newsDate = new Date(news.date);
      return newsDate >= new Date(startDate) && newsDate <= new Date(endDate);
    });
  }

  return filteredNews;
};

export const getNewsById = async (id: string): Promise<News | undefined> => {
  // Simulate API delay
  await new Promise(resolve => setTimeout(resolve, 500));
  return newsData.news.find(news => news.id === id);
}; 